              &#x3C;body&#x3E;
              &#x3C;/body&#x3E;
              &#x3C;/html&#x3E;
            </code>
          </pre> 
        </section>
        <section>
          <h2>특수기호 Entity name</h2>
          <p>특수기호는 Entity name을 사용하여 entity 코드로 변환한다.</p>
        </section>
        <section>
          <h2>시멘틱 마크업(Semantic Markup)</h2>
          <div class="layout_table col_tabel">
            <table>
                <caption class="blind">Semantics Markup</caption>
                <colgroup>
                    <col style="width:15%">
                    <col style="width:25%">
                    <col style="width:20%">
                    <col style="width:auto">
                </colgroup>
                <thead>
                    <tr>
                        <th scope="col">종류</th>
                        <th scope="col">구분</th>
                        <th scope="col">태그</th>
                        <th scope="col">설명</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td rowspan="2">그룹</td>
                        <td>블록타입</td>
                        <td>div</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>인라인타입</td>
                        <td>span</td>
                        <td></td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="5">랜드마크</td>
                        <td>머리글</td>
                        <td>header</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>바닥글</td>
                        <td>footer</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">연속적인 컨텐츠그룹</td>
                        <td class="red_font">section</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">독립적인 컨텐츠그룹</td>
                        <td class="red_font">article</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>보조적인 컨텐츠그룹</td>
                        <td>aside</td>
                        <td></td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="6">목차</td>
                        <td>목차 1레벨</td>
                        <td>h1</td>
                        <td>페이지 제목 최상위 레벨(문서 필수 요소)</td>
                    </tr>
                    <tr>
                        <td>목차 2레벨</td>
                        <td>h2</td>
                        <td>페이지 제목 수준</td>
                    </tr>
                    <tr>
                        <td>목차 3레벨</td>
                        <td>h3</td>
                        <td>컨텐츠 핵심 제목 수준</td>
                    </tr>
                    <tr>
                        <td>목차 4레벨</td>
                        <td>h4</td>
                        <td>컨텐츠 부 제목 수준</td>
                    </tr>
                    <tr>
                        <td>목차 5레벨</td>
                        <td>h5</td>
                        <td>컨텐츠 상세 제목 수준</td>
                    </tr>
                    <tr>
                        <td>목차 6레벨</td>
                        <td>h6</td>
                        <td>컨텐츠 상세 제목 수준</td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="8">컨텐츠<br>(Block Type)</td>
                        <td>순서 없는 리스트</td>
                        <td>ul, li</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>순서 있는 리스트</td>
                        <td>ol, li</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>정의 리스트</td>
                        <td>dl, dt, dd</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>단락</td>
                        <td>p</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>주소</td>
                        <td>address</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">사진의 내용 표시</td>
                        <td class="red_font">figure</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">사진의 제목 표시</td>
                        <td class="red_font">figcaption</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">대화상자</td>
                        <td class="red_font">dialog</td>
                        <td>호환성 IE Not Supported (role="dialog" 대체사용)</td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="8">컨텐츠<br>(Inline Type)</td>
                        <td>링크</td>
                        <td>a</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>중요한</td>
                        <td>strong</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>일반강조</td>
                        <td>em</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>의미없는 마크업</td>
                        <td>i</td>
                        <td>실제 용도에 의미를 두지는 않으며 아이콘 등에 사용한다.</td>
                    </tr>
                    <tr>
                        <td class="red_font">정의</td>
                        <td class="red_font">dfn</td>
                        <td>title속성을 사용하여 용어를 정의한다.</td>
                    </tr>
                    <tr>
                        <td>작은 텍스트</td>
                        <td>small</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">취소</td>
                        <td class="red_font">s</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td class="red_font">삭제</td>
                        <td class="red_font">del</td>
                        <td></td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="5">서식<br></td>
                        <td>서식 그룹</td>
                        <td>fieldset</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>서식 제목</td>
                        <td>legend</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>라벨</td>
                        <td>label</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>입력, 선택 컨트롤</td>
                        <td>input, textarea, select</td>
