                    </tr>
                    <tr>
                        <td>버튼</td>
                        <td>button</td>
                        <td>일반 버튼타입과 전송타입의 버튼을 구분한다.</td>
                    </tr>
                </tbody>
                <tbody>
                    <tr>
                        <td rowspan="10">표<br></td>
                        <td>표</td>
                        <td>table</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 제목</td>
                        <td>caption</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 열 그룹</td>
                        <td>colgroup</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 열</td>
                        <td>col</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 헤더</td>
                        <td>thead</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 본문</td>
                        <td>tbody</td>
                        <td></td>
                    </tr>
                    <tr>
