                        <td>표 행</td>
                        <td>tr</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 제목셀</td>
                        <td>th</td>
                        <td></td>
                    </tr>
                    <tr>
                        <td>표 내용셀</td>
                        <td>td</td>
                        <td></td>
                    </tr>
                </tbody>
            </table>
        </div>
        </section>
             
      </div>
    </main>
  </template>
  
  <script>
  export default {
    name: 'Standard',
    data () {
      return {
      }
    },
    directives: {
        Highlight
    },

    methods:{
        btnClickCopy: function(){
            const htmlTag = document.querySelector( ".language-html" ).textContent;
            const textarea = document.createElement('textarea');
            textarea.textContent = htmlTag;
            document.body.append(textarea);
            textarea.select();
            document.execCommand('copy');      
            textarea.remove();
            alert( "텍스트 복사완료" ); 
        }
    }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="scss" scoped>
    main{
      width:100%;
      padding-left: 250px;
      padding-bottom: 200px;
      box-sizing: border-box;
      .main-inner{
        max-width: 860px;
        margin: 0 auto;
        section{
          margin-top: 40px;
        h2{
         
          font-size: 34px;
          color: #111;
          font-weight: 700;
          margin-bottom: 30px;
        }
  
        h3{
          position: relative;
          margin-top: 20px;
          padding: 0 0 20px 10px;
          font-size: 24px;
          font-weight: 400;
          color: #4b77be;
          letter-spacing: -1px;
          &:before{
            display: inline-block;
            content: '';
            position: absolute;
            top:0;
            left: 0;
            width:5px;
            height: 30px;
            background: #4b77be;
          }
        }

        .button-content{
            display: flex;
            justify-content: flex-end;
            button{
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                background: #0d6efd;
                color: #fff;
                margin: 0;
                padding: 10px 20px;
                font-family: 'Noto Sans KR', sans-serif;
                font-size: 1rem;
                font-weight: 400;
                text-align: center;
                text-decoration: none;
                
                border: none;
                border-radius: 4px;
                
                display: inline-block;
                width: auto;
                cursor: pointer;

            }
        }
  
  
        .custom-block{
          margin-top: 30px;
          border-color: #00a1ed;
          padding: 20px 30px;
          font-size:16px;
          background-color: #f3f5f7;
          border-left-width: 8px;
          border-left-style: solid;
          letter-spacing: -1px;
          p{
            line-height: 1.7;
            font-weight:300;
            a{ color:#00a1ed; }
          }
        }
  
  
        .custom-table{
          font-size:16px;
        }
        .layout_table{
          border-top: 1px solid #000;
          border-bottom: 1px solid #000;
          table {
            width:100%;
            th,
            td {padding: 10px; vertical-align: middle; border: 1px solid #ddd;}
            tbody{
              tr{
                th{
                  border-left: none;
                  background: #f3f3f3;
                }
                td{
                  border-right: none;
                }
              }
  
            }
          }
          &.col_tabel{
            table{
              tr{
                th{background: #46637f; border-bottom: 0; color: #fff; font-weight: bold;}
              }
            }
          }
        }
  
        }
    }
  }
  </style>
  


