<template>
  <main >
    <div class="main-inner">
      <section>
        <h2>코드규칙</h2>
        <h3>HTML 규칙</h3>
        <div class="code-rule">
          <strong>들여쓰기</strong>
          <ul>
            <li>1탭 들여쓰기 하며, 1탭의 크기는 공백 4칸으로 설정한다.</li>
          </ul>
        </div>
        <div class="code-rule">
          <strong>빈줄</strong>
          <ul>
            <li>가독성을 고려하여 빈 줄을 추가한다. 단, 1줄을 초과하지 않는다.</li>
          </ul>
        </div>
        <div class="code-rule">
          <strong>주석</strong>
          <ul>
            <li>시작과 끝의 표시는 s: e: 로 구분한다.</li>
          </ul>
        </div>        
      </section>

      <section>
        <h3>속성 선언 순서</h3>
        <div class="layout_table col_tabel">
          <table>
              <caption class="blind">HTML 속성선언순서</caption>
              <colgroup>
                  <col style="width:20%;">
                  <col>
                  <col style="width:20%;">
              </colgroup>
              <thead>
                  <tr>
                      <th>구분</th>
                      <th>속성</th>
                      <th>속성 값</th>
                  </tr>
              </thead>
              <tbody>
                  <tr>
                      <td>1. 기능</td>
                      <td>href, src, type</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>2. 이름</td>
                      <td>name, id</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>3. 정보</td>
                      <td>value, title, alt</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>4. 옵션</td>
                      <td>target, tabindex</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>5. 꾸미기</td>
                      <td>class</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>6. 역할</td>
                      <td>role</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>7. 속성</td>
                      <td>aria-controls, aria-describedby, aria-labelledby, aria-popup, aria-modal 등</td>
                      <td></td>
                  </tr>
                  <tr>
                      <td>8. 상태</td>
                      <td>checked, disabled, selected, required, aria-selected, aria-current, aria-hidden
                          등
                      </td>
                      <td></td>
                  </tr>
              </tbody>
          </table>
        </div>
      </section>

      <section>
        <h3>CSS 규칙</h3>
        <h4>작은 따음표 사용범위</h4>
        <div class="code-rule">
          <ul>
            <li>폰트의 선언, filter, content에 작은 따옴표('')를 사용한다.</li>
          </ul>
        </div>
          <pre v-highlightjs>
            <code class="language-css hljs">
              .class-name {font-family:'dotum';}
              .filter:progid:DXImageTransform.Microsoft.AlphaImageLoader(src='img.png', sizingMethod='scale';}
              .class-name:after{content:'.'}
              .class-name {background:url(img.gif) no-repeat}
            </code>
          </pre> 
          <h4>세미콜론</h4>
          <div class="code-rule">
            <ul>
              <li>마지막 선언된 속성에도 세미콜론(;)을 사용한다.</li>
            </ul>
          </div>
          <h4>공백의 사용</h4>
          <div class="code-rule">
            <ul>
              <li>선택자 간, 중괄호간 한칸의 공백을 사용한다, 중괄호 안쪽 좌우에는 공백은 사용하지 않는다.</li>
            </ul>
          </div>
          <pre v-highlightjs>
            <code class="language-css hljs">
              .class-name,
              .class-name .child-class {background:url(../img/img.gif) no-repeat;}
              .class-name {padding:0; margin:0;}
              .class-name {padding:0; margin:0;}
            </code>
          </pre> 
          <h4>들여쓰기 줄바꿈</h4>
          <div class="code-rule">
            <ul>
              <li>CSS 코드 작성시 들여쓰기는 허용하지 않는다.</li>
              <li>공통으로 사용되는 속성의 선택자간의 줄 바꿈은 허용하며 이 외의 줄바꿈은 허용하지 않는다.</li>
            </ul>
          </div>
          <pre v-highlightjs>
            <code class="language-css hljs">
              h4.class-name {font-size:1.25em;letter-spacing:-1px;margin:0.5em 0 0 0;}

              .class-name,
              .class-name .child-class {background:url(../img/img.gif) no-repeat;}
            </code>
          </pre> 
          <h4>주석 빈줄</h4>
          <pre v-highlightjs>
            <code class="language-css hljs">
              /* title */
              .title-type1 {margin:30px 0 10px 0;}
              
              /* description */
              .desc1 {padding:2px 0 0 15px;font-size:0.917em;}
            </code>
          </pre> 
      </section>
      <section>
        <h4>속성 선언 순서</h4>
        
        <div class="layout_table col_tabel">
          <table>
              <caption class="blind">CSS 속성선언순서</caption>
              <colgroup>
                  <col style="width:20%;">
                  <col style="width:20%;">
                  <col>
              </colgroup>
              <thead>
                  <tr>
                      <th>구분</th>
                      <th>속성</th>
                      <th>속성 값</th>
                  </tr>
              </thead>
              <tbody>
                  <tr>
                      <td rowspan="2">1. 표시</td>
                      <td>display</td>
                      <td>none, block, inline, inline-block, inline-table, list-item, table,
                          table-caption,
                          table-cell, table-column, table-column-group, table-footer-group,
                          table-header-group,
                          table-row, table-row-group</td>
                  </tr>
                  <tr>
                      <td>visibility</td>
                      <td>visible, hidden, collapse</td>
                  </tr>
                  <tr>
                      <td rowspan="3">2. 위치</td>
                      <td>position</td>
                      <td>absolute, fixed, relative, static</td>
                  </tr>
                  <tr>
                      <td>top, right, bottom, left</td>
                      <td>length(px), %</td>
                  </tr>
                  <tr>
                      <td>z-index</td>
                      <td>number</td>
                  </tr>
                  <tr>
                      <td rowspan="2">3. 넘침/흐름</td>
                      <td>overflow</td>
                      <td>visible, hidden, scroll, auto</td>
                  </tr>
                  <tr>
                      <td>float</td>
                      <td>left, right, none</td>
                  </tr>
                  <tr>
                      <td rowspan="4">4. 크기/간격</td>
                      <td>width, max-width, min-width</td>
                      <td>auto, length(px), %</td>
                  </tr>
                  <tr>
                      <td>height, max-height, min-height</td>
                      <td>auto, length(px), %</td>
                  </tr>
                  <tr>
                      <td>margin</td>
                      <td>auto, length(px, em), %</td>
                  </tr>
                  <tr>
                      <td>padding</td>
                      <td>auto, length(px, em), %</td>
                  </tr>
                  <tr>
                      <td rowspan="2">5. 박스모양</td>
                      <td>border</td>
                      <td>border-width, border-style, border-color</td>
                  </tr>
                  <tr>
                      <td>background</td>
                      <td>background-color, background-image, background-repeat, background-attachment,
                          background-position</td>
                  </tr>
                  <tr>
                      <td rowspan="4">6. 폰트/정렬</td>
                      <td>font</td>
                      <td>font-style, font-variant, font-weigth, font-size/line-height, font-family</td>
                  </tr>
                  <tr>
                      <td>color</td>
                      <td>hex_number, hex_number</td>
                  </tr>
                  <tr>
                      <td>text-align</td>
                      <td>left, right, center, justify</td>
                  </tr>
                  <tr>
                      <td>vertical-align</td>
                      <td>top, middle, bottom, baseline</td>
                  </tr>
                  <tr>
                      <td>7. 기타</td>
                      <td></td>
                      <td>언급되지 않은 속성들은 후순위로 적용한다.</td>
                  </tr>
              </tbody>
          </table>
      </div>
      </section>

    </div>
  </main>
</template>

<script>
export default {
  name: 'name',
  data () {
    return {
    }
  },
  directives: {
      Highlight
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
 @import '@/assets/scss/common/table.scss';
  main{
    width:100%;
    padding-left: 250px;
    padding-bottom: 200px;
    box-sizing: border-box;
    .main-inner{
      max-width: 860px;
      margin: 0 auto;
      section{
        margin-top: 40px;
      h2{
       
        font-size: 34px;
        color: #111;
        font-weight: 700;
        margin-bottom: 30px;
      }

      h3{
        position: relative;
        margin: 20px 0;
        padding: 0 0 10px 10px;
        font-size: 24px;
        font-weight: 400;
        color: #4b77be;
        letter-spacing: -1px;
        &:before{
          display: inline-block;
          content: '';
          position: absolute;
          top:0;
          left: 0;
          width:5px;
          height: 30px;
          background: #4b77be;
        }
      }


      h4{
        margin: 20px 0;
        font-size: 20px;
        font-weight: 500;
        color: #4b77be;
        letter-spacing: -1px;
      }

      .code-rule{ 
        padding-bottom: 20px;
        strong{
          display: block;
          margin-bottom: 10px;
          font-weight: bold;
          font-size:20px;
        }
        ul{
            li{ 
              position: relative;
              padding-left: 20px; 
              &:after{
                content: ''; 
                display: block;
                width: 5px;
                height: 5px;
                background: #000;
                border-radius: 10px;
                position: absolute;
                top:5px;
                left:10px;
                
              }
            }
          }
        }


    

      }
  }
}
</style>



