<template>
    <header >
      <h1 class="nav-title">
        <a href="">
          <img src="@/assets/images/common/logo.svg" alt="플그림">
          <span>2022.10</span>
        </a>
      </h1>
      <ul class="nav">
        <li class="on">
          <span>Guide</span>
          <!-- <router-link to="/" tag="a">PC</router-link> -->
          <!-- <a href="javascript:;">PC</a> -->
          <ul>
            <li  v-for="nav in navigations" :key="nav.name">
              <router-link :to="nav.href" tag="a" exact-active-class="on">{{ nav.name}}</router-link>
            </li>
          </ul>
        </li>
        <li>
            <a href="javascript:;">Script</a>
        </li>
    </ul>
    </header>
  </template>
  
  <script>
  export default {
    name: 'Header',
    data () {
      return {
        navigations: [
            {
              name: '기본정책',
              href: '/',
              
            },
            {
              name: '표준규칙',
              href: '/Layout/Standard'
            },
            {
              name: '코드규칙',
              href: '/Layout/Rule'
            },
            {
              name: '네임규칙',
              href: '/Layout/Name'
            }
         ]
      }
    },
    created() {
    },
    mounted() {
      this.$nextTick( function(){
          this.urlCheckEvent();
      })
    },
    methods: {
      urlCheckEvent : function(){
        
      }
    }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="scss" scoped>
    header{
      position: fixed;
      z-index: 99;
      overflow: hidden;
      overflow-y: auto;
      left: 0;
      top: 0;
      width: 250px;
      height: 100%;
      background: #FFF;
      box-shadow: 1px 0 20px RGB(0 0 0 / 8%);
      .nav-title{
        background:#F7FAFC;
        padding: 20px 25px;
        border-bottom: 1px solid #DDD;
        color: #000;
        font-size: 20px;
        letter-spacing: -1px;
        font-weight: bold;
        a{
          display: flex;
          align-items: baseline;
          img{
            display: inline-block;
            width: 70px;
          }
          span{
            margin-left: 10px;
            font-size: 12px;
            color: #666;
            font-weight: 400;
            letter-spacing: 0;
          }
        }
      }
      .nav{
        li{
          border-bottom: 1px solid #edeff0;
          span{
            position: relative;
            font-size: 18px;
            color: #666;
            display: block;
            padding: 15px 35px 15px 25px;
            border-bottom: 1px solid #edeff0;
          }
          a{
            position: relative;
            font-size: 18px;
            color: #666;
            display: block;
            padding: 15px 35px 15px 25px;
          }
          ul{
            li{
              padding-left: 10px;
            }
          }
          &.on{
            >a{
              padding-left: 20px;
              color: #000;
              border-left: 5px solid #01A0E9;
              background: #f5f6f7;
            }
            ul{
              li{
                a{
                  &.on{
                    font-weight: bold;
                    &:before{
                      content: '';
                      display: block;
                      position:absolute;
                      top:22px;
                      left:14px;
                      width: 5px;
                      height: 5px;
                      background: #00a1ed;
                      border-radius: 10px;
                    }
                  } 
                }
              }
            }
          }
        }
      }
    }
  </style>
  

  


