import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home";
import Standard from "@/views/Standard";
import Rule from "@/views/Rule";
import Name from "@/views/Name";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      component: Home,
      name: "Home"
    },
    {
      path: "/index",
      component: Home,
      name: "Home"
    },
    {
      path: "/Layout/Standard",
      component: Standard,
      name: "Standard"
    },
    {
      path: "/Layout/Rule",
      component: Rule,
      name: "Rule"
    },
    {
      path: "/Layout/Name",
      component: Name,
      name: "Name"
    }
  ]
});



